/*标题，抬头css*/
.webmain .con {
  margin-bottom: 45px; }

.webmain .con .title {
  height: 60px;
  overflow: hidden;
  background: url("../../../images/line_contitle.gif") center center repeat-x;
  margin-bottom: 15px;
  position: relative; }

.webmain .con .title a.more {
  display: block;
  position: absolute;
  right: 0;
  top: 16px;
  background: url("../../../images/more_contitle.gif");
  width: 130px;
  height: 30px;
  text-indent: -99em;
  overflow: hidden; }

.webmain .con .title h2 {
  display: block;
  width: 100px;
  height: 60px;
  background-image: url("../../../images/sprite_starwars.jpg");
  background-repeat: no-repeat;
  text-indent: -99em;
  overflow: hidden;
  float: left; }

.webmain .con .title ul {
  float: left;
  height: 26px;
  background: #fff;
  padding: 17px 5px 17px 5px; }

.webmain .con .title ul li {
  float: left;
  font-size: 16px;
  height: 26px;
  line-height: 26px;
  overflow: hidden;
  margin-right: 8px; }

.webmain .con .title ul li a {
  display: block;
  width: 100%;
  height: 26px;
  line-height: 26px;
  cursor: pointer;
  padding: 0 8px; }

.webmain .con .title ul li.c a {
  background: #000;
  color: #fff; }

/*banner轮播*/
.banner {
  position: relative;
  width: 1200px;
  height: 530px;
  text-align: center;
  overflow: hidden; }
  .banner .imglist li, .banner .imglist li img {
    width: 1200px;
    height: 530px; }
  .banner .imglist {
    position: absolute;
    left: 0;
    height: 530px; }
    .banner .imglist li {
      float: left; }
  .banner .dotlist {
    display: inline-block;
    position: relative;
    top: 480px; }
    .banner .dotlist li {
      float: left;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: url("../../../images/sprite_dot.png") 0 -12px no-repeat;
      margin-left: 10px;
      margin-right: 10px; }
      .banner .dotlist li.active {
        background: url("../../../images/sprite_dot.png") 0 0 no-repeat; }

.webmain .judge .judge-title h2 {
  width: 190px; }
.webmain .judge .judge-title li {
  width: 110px;
  height: 25px;
  text-align: center;
  cursor: pointer; }
  .webmain .judge .judge-title li.active {
    color: #fff;
