.webmain .judge-content .item, .webmain .judge-content .item img, .webmain .judge-content .item sub {
  width: 125px;
  height: 142px; }

.webmain .judge-content {
  width: 1228px;
  margin-left: -28px; }
  .webmain .judge-content li {
