.webmain .starrecommend-content .item, .webmain .starrecommend-content .item img, .webmain .starrecommend-content .item sub {
  width: 280px;
  height: 320px; }

.webmain .starrecommend .starrecommend-title h2 {
  width: 180px;
  background-position: 0 -60px; }

.webmain .starrecommend-content {
  width: 1226px;
  margin-left: -26px; }
  .webmain .starrecommend-content .item {
